import Vue from 'vue'
import Router from 'vue-router'
import task1 from '@/components/task1/task1.vue'
import task2 from '@/components/task2/task2.vue'
import task3 from '@/components/task3/task3.vue'
import task4 from '@/components/task4/task4.vue'
import task5 from '@/components/task5/task5.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'task1',
      component: task1
    },
    {
      path: '/task2',
      name: 'task2',
      component: task2
    },
    {
      path: '/task3',
      name: 'task3',
      component: task3
    },
    {
      path: '/task4',
      name: 'task4',
      component: task4
    },
    {
      path: '/task5',
      name: 'task5',
      component: task5
    }
  ]
})



