<template>
    <div class="header">
       <!-- <div class="t_list" @click="menuClick(1)">页面1</div>
       <div class="t_list" @click="menuClick(2)">页面2</div>
       <div class="t_list" @click="menuClick(3)">页面3</div>
       <div class="t_list" @click="menuClick(4)">页面4</div>
       <div class="t_list" @click="menuClick(5)">页面5</div> -->
       <div class="t_list" v-for="(item,index) in menuList" :key="index" @click="menuClick(item.value)">
           {{item.name}}
       </div>
    </div>
</template>

<script>
export default {
    name:'HelloWorld',
    data() {
        return {
            msg: 'header',
            menuList:[]
        }
    },
    created(){

    },
    mounted(){
        this.menuList =[
            {
                name:'页面1',
                value:1
            },
            {
                name:'页面2',
                value:2
            },
            {
                name:'页面3',
                value:3
            },
            {
                name:'页面4',
                value:4
            },
            {
                name:'页面5',
                value:5
            }
        ];
    },
    methods:{
        menuClick:function(num){
            console.log(num);
            if(num==1){
                this.$router.push("/");
            }else{
                this.$router.push("task"+num);
            }
        }
    },
    computed:{

    },
    watch:{
        
    }

}
</script>



<style scoped>
.t_list{
    width: 100%;
    height: 60px;
    margin-top:20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border: 1px solid #ccc;
    border-radius: 5px;
}
</style>




